export default {
    name: "Projects",
    components: {},
    data() {
        return {
            selectedPage: 'PROJECTS',
        }
    },
    methods: {
        select(event) {
            event.stopPropagation();
            event.preventDefault();
        },
        handleClick(_selectedPage) {
            this.selectedPage = _selectedPage;
            document.body.scrollTop = 0;
            document.documentElement.scrollTop = 0;
        }
    }
};
