var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"flex flex-col bg-white"},[_vm._ssrNode(((_vm.selectedPage === 'PROJECTS')?("<div class=\"flex flex-col container mx-auto pt-20 pb-20\"><h1 class=\"w-full flex justify-center text-6xl pb-4 font-raleway\">Projects</h1> <div class=\"flex flex-1 w-full justify-center \"><div class=\"title-line bg-black\"></div></div></div>"):"<!---->")+" "+((_vm.selectedPage === 'PROJECTS')?("<div class=\"flex flex-1 container mx-auto pb-20\"><div class=\"flex flex-1 w-full justify-center\"><div class=\"grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8\"><div class=\"w-80 h-80 bg-grey-green transition duration-500 hover:bg-gray-500\"><a href=\"#\" id=\"realistic-logo\" class=\"h-full w-full flex items-center justify-center text-center hover-logo\"></a></div> <div class=\"place-self-auto h-80 bg-grey-green transition duration-500 hover:bg-gray-500\"><a href=\"#\" id=\"aico-logo\" class=\"h-full w-full flex items-center justify-center text-center hover-logo\"></a></div> <div class=\"place-self-auto h-80 bg-grey-green transition duration-500 hover:bg-gray-500\"><a href=\"#\" id=\"gent-logo\" class=\"h-full w-full flex items-center justify-center text-center hover-logo\"></a></div> <div class=\"place-self-auto h-80 bg-grey-green transition duration-500 hover:bg-gray-500\"><a href=\"#\" id=\"gjidi-logo\" class=\"h-full w-full flex items-center justify-center text-center hover-logo\"></a></div> <div class=\"place-self-auto h-80 bg-grey-green transition duration-500 hover:bg-gray-500\"><a href=\"#\" id=\"apoti-logo\" class=\"h-full w-full flex items-center justify-center text-center hover-logo\"></a></div> <div class=\"place-self-auto h-80 bg-grey-green transition duration-500 hover:bg-gray-500\"><a href=\"#\" id=\"nimuphot-logo\" class=\"h-full w-full flex items-center justify-center text-center hover-logo\"></a></div> <div class=\"place-self-auto h-80 bg-grey-green transition duration-500 hover:bg-gray-500\"><a href=\"#\" id=\"investigo-logo\" class=\"h-full w-full flex items-center justify-center text-center hover-logo\"></a></div> <div class=\"place-self-auto h-80 bg-grey-green transition duration-500 hover:bg-gray-500\"><a href=\"#\" id=\"banvirte-logo\" class=\"h-full w-full flex items-center justify-center text-center hover-logo\"></a></div> <div class=\"place-self-auto h-80 bg-grey-green transition duration-500 hover:bg-gray-500\"><a href=\"#\" id=\"atila-logo\" class=\"h-full w-full flex items-center justify-center text-center hover-logo\"></a></div> <div class=\"place-self-auto h-80 bg-grey-green transition duration-500 hover:bg-gray-500\"><a href=\"#\" id=\"saibsal-logo\" class=\"h-full w-full flex items-center justify-center text-center hover-logo\"></a></div></div></div></div>"):"<!---->")+" "+((_vm.selectedPage === 'PROJECTS')?("<div class=\"flex flex-col container mx-auto pt-20 pb-20\"><h1 class=\"w-full flex justify-center text-6xl pb-4 font-raleway\">Awards</h1> <div class=\"flex flex-1 w-full justify-center \"><div class=\"title-line bg-black\"></div></div></div>"):"<!---->")+" "+((_vm.selectedPage === 'PROJECTS')?("<div class=\"flex flex-1 container mx-auto pb-20\"><div class=\"flex flex-1 w-full justify-center\"><div class=\"grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8\"><div class=\"w-80 h-80 bg-grey-green transition duration-500 hover:bg-gray-500\"><div id=\"valbiocar-logo\" class=\"h-full w-full flex items-center justify-center text-center hover-logo\"></div></div> <div class=\"w-80 h-80 bg-grey-green transition duration-500 hover:bg-gray-500\"><div id=\"bioevooc-logo\" class=\"h-full w-full flex items-center justify-center text-center hover-logo\"><span class=\"text-5xl font-raleway\">BIOEVOOC</span></div></div></div></div></div>"):"<!---->")+" "+((_vm.selectedPage === 'REALISTIC')?("<div class=\"flex container mx-auto pt-20 pb-20 h-auto md:w-2/3\"><div class=\"w-4/6 flex flex-col h-auto\"><a href=\"#\" title=\"Go Back\"><svg xmlns=\"http://www.w3.org/2000/svg\" fill=\"none\" viewBox=\"0 0 24 24\" stroke-width=\"1.5\" stroke=\"currentColor\" class=\"w-10 h-10\"><path stroke-linecap=\"round\" stroke-linejoin=\"round\" d=\"M11.25 9l-3 3m0 0l3 3m-3-3h7.5M21 12a9 9 0 11-18 0 9 9 0 0118 0z\"></path></svg></a> <h1 class=\"w-full flex  text-6xl pt-16 pb-16 font-raleway\">REALISTIC</h1> <p class=\"flex w-full pb-28 font-avenir pr-8 text-justify\">The main focus of REALISTIC is the 3D processing\n                and creation of realistic anatomical models and orthoses\n                of complex geometry with novel polymeric materials. This is intended to enable personalized surgical\n                planning and to reduce the side effects\n                of treatments. In REALISTIC, the BTELab collaborates with an exceptional group of clinicians, the\n                Technological Institute of Plastics (AIMPLAS),\n                and the Valencian company leader in 3D technological solutions (IT3D) to present an integrative and\n                transversal proposal that not only focuses\n                on production but also validates the work done in clinical environments. REALISTIC aims to potentially\n                establish its own integral pilot unit\n                for the generation of surgical planning models and highly complex geometric prostheses, from which\n                healthcare professionals from different\n                specialties of the General Hospital, as well as from other services and collaborating hospitals, will\n                benefit. For this purpose, high quality\n                innovative materials synthesized and improved by AIMPLAS will be used, with the support and assistance\n                of IT3D.</p> <p class=\"font-avenir font-semibold\">IP: Carmen Escobedo Lucea</p> <div class=\"flex flex-1 items-center justify-center\"><img"+(_vm._ssrAttr("src",require("assets/realistic_logo.webp")))+" alt=\"Realistic\" class=\"object-none object-center w-64\"></div></div> <div class=\"w-2/6 border-l-2 border-gray-300\"><img"+(_vm._ssrAttr("src",require("assets/logos_realistic.png")))+" alt=\"Logos Realistic project\" class=\"relative top-40\"></div></div>"):"<!---->")+" "+((_vm.selectedPage === 'AICO')?("<div class=\"flex container mx-auto pt-20 pb-20 h-auto md:w-2/3\"><div class=\"w-4/6 flex flex-col h-auto\"><a href=\"#\" title=\"Go Back\"><svg xmlns=\"http://www.w3.org/2000/svg\" fill=\"none\" viewBox=\"0 0 24 24\" stroke-width=\"1.5\" stroke=\"currentColor\" class=\"w-10 h-10\"><path stroke-linecap=\"round\" stroke-linejoin=\"round\" d=\"M11.25 9l-3 3m0 0l3 3m-3-3h7.5M21 12a9 9 0 11-18 0 9 9 0 0118 0z\"></path></svg></a> <h1 class=\"w-full flex  text-6xl pt-16 pb-16 font-raleway\">AICO</h1> <p class=\"flex w-full pb-28 font-avenir pr-8 text-justify\">The purpose of these grants is to promote the\n                research activity of established R&amp;D&amp;I research groups\n                that show growth potential to become reference groups.</p> <p class=\"font-avenir font-semibold\">IP: Carmen Escobedo Lucea</p></div> <div class=\"w-2/6 border-l-2 border-gray-300\"><img"+(_vm._ssrAttr("src",require("assets/logos_aico.png")))+" alt=\"Logos AICO project\" class=\"relative top-40\"></div></div>"):"<!---->")+" "+((_vm.selectedPage === 'GENT')?("<div class=\"flex container mx-auto pt-20 pb-20 h-auto md:w-2/3\"><div class=\"w-4/6 flex flex-col h-auto\"><a href=\"#\" title=\"Go Back\"><svg xmlns=\"http://www.w3.org/2000/svg\" fill=\"none\" viewBox=\"0 0 24 24\" stroke-width=\"1.5\" stroke=\"currentColor\" class=\"w-10 h-10\"><path stroke-linecap=\"round\" stroke-linejoin=\"round\" d=\"M11.25 9l-3 3m0 0l3 3m-3-3h7.5M21 12a9 9 0 11-18 0 9 9 0 0118 0z\"></path></svg></a> <h1 class=\"w-full flex  text-6xl pt-16 pb-16 font-raleway\">PLAN GENT</h1> <p class=\"flex w-full pb-28 font-avenir pr-8 text-justify\">Worldwide, 300 million patients with severe\n                wounds, including deep burns and chronic wounds such as foot ulcers\n                and pressure sores, undergo surgery annually. Unfortunately, this number will continue growing due to\n                the increase of life expectancy and chronic\n                diseases. Autografting, the transplantation of skin from the same patient, is the ‚Äògold standard‚Äô\n                treatment. But the lack of healthy donor sites\n                in patients with extensive deep burns makes it necessary to use skin from cadaver or animals, which\n                carries some risks of infection and rejection.\n                Tissue-engineered (TE) skin has emerged as promising alternative to solve these problems. But, up to\n                now, none TE skin substitute created with own\n                patient cells can fully replicate an autograft and become a permanent skin replacement. The inability to\n                vascularize always leads to the dislodgement\n                of the transplant. The last decade has seen remarkable progress in the collective effort to bioengineer\n                such networks by selfassembly of vascular\n                cells within suitable biomaterial that leaded graft resorption and failure. Heterotypic interactions\n                between ECs and MSCs/perivascular cells are\n                crucial in functional vascular development and stability in vivo. In 2D culture, however, these two\n                cells types are separated, and thus crosstalk\n                between them. Restoring this heterotypic cellular interaction in vitro is required for establish a\n                functional vasculature in in vitro systems.\n                Additionally, whether and how signals from endothelial cells influence the fate-restricted\n                characteristics of MSCs /perivascular cells during\n                vascularization process remains unclear.We aim to tackle these problems with an innovative strategy\n                called Xeno-SPATE to: (1) build autologous skin\n                constructs with functional channels, (2) to enlighten the role of EVs as cell-cell communication\n                mediators during dermal vascular development\n                and discover the cargo molecules involved (3) to pre-vascularize the skin substitute.</p> <p class=\"font-avenir font-semibold\">IP: Carmen Escobedo Lucea</p></div> <div class=\"w-2/6 border-l-2 border-gray-300\"><img"+(_vm._ssrAttr("src",require("assets/logos_plangent.png")))+" alt=\"Logos Plan Gent project\" class=\"relative top-40\"></div></div>"):"<!---->")+" "+((_vm.selectedPage === 'GJIDI')?("<div class=\"flex container mx-auto pt-20 pb-20 h-auto md:w-2/3\"><div class=\"w-4/6 flex flex-col h-auto\"><a href=\"#\" title=\"Go Back\"><svg xmlns=\"http://www.w3.org/2000/svg\" fill=\"none\" viewBox=\"0 0 24 24\" stroke-width=\"1.5\" stroke=\"currentColor\" class=\"w-10 h-10\"><path stroke-linecap=\"round\" stroke-linejoin=\"round\" d=\"M11.25 9l-3 3m0 0l3 3m-3-3h7.5M21 12a9 9 0 11-18 0 9 9 0 0118 0z\"></path></svg></a> <h1 class=\"w-full flex  text-6xl pt-16 pb-16 font-raleway\">GJIDI</h1> <p class=\"flex w-full pb-28 font-avenir pr-8 text-justify\">Call of Order 3/2018 of the Regional Ministry of\n                Education, Research, Culture and Sport, within the\n                framework of the National Youth Guarantee System and co-financed by the European Social Fund and the\n                Youth Employment Initiative, whose main\n                objective is to improve the training and employability of R&amp;D technical and management personnel. This\n                grant seeks to strengthen research\n                activities in public research centers, as well as to improve the performance of infrastructures,\n                scientific-technical equipment, laboratories\n                and other facilities or services of a general nature or of common use of the entity.<br><br> This\n                funding offers the opportunity to hire qualified\n                personnel in the field of R&amp;D, thus contributing to increase human resources at the beneficiary\n                entities. The contracts granted allow the\n                incorporation of technical and management personnel, in order to promote research activities and improve\n                the scientific and technological\n                capacity of the institutions. In addition to favoring the employability of young people, this call has a\n                special focus on the development of\n                research projects and the optimization of facilities and services related to science and technology.</p> <p class=\"font-avenir font-semibold\">IP: Carmen Escobedo Lucea</p></div> <div class=\"w-2/6 border-l-2 border-gray-300\"><img"+(_vm._ssrAttr("src",require("assets/logos_gjidi.png")))+" alt=\"Logos GJIDI project\" class=\"relative top-40\"></div></div>"):"<!---->")+" "+((_vm.selectedPage === 'APOTI')?("<div class=\"flex container mx-auto pt-20 pb-20 h-auto md:w-2/3\"><div class=\"w-4/6 flex flex-col h-auto\"><a href=\"#\" title=\"Go Back\"><svg xmlns=\"http://www.w3.org/2000/svg\" fill=\"none\" viewBox=\"0 0 24 24\" stroke-width=\"1.5\" stroke=\"currentColor\" class=\"w-10 h-10\"><path stroke-linecap=\"round\" stroke-linejoin=\"round\" d=\"M11.25 9l-3 3m0 0l3 3m-3-3h7.5M21 12a9 9 0 11-18 0 9 9 0 0118 0z\"></path></svg></a> <h1 class=\"w-full flex  text-6xl pt-16 pb-16 font-raleway\">APOTI</h1> <p class=\"flex w-full pb-28 font-avenir pr-8 text-justify\">The aim of the project is to analyze the\n                biosafety of breast implants by comparing different types\n                of prostheses (silicone gel-filled PIP implants, non-PIP silicone gel-filled breast implants and\n                saline-filled implants). The inflammatory\n                reaction that these materials may cause in the normal or capsular tissue surrounding the implant has\n                been investigated. A detailed study of\n                these areas at the molecular and microscopic level is sought and the results contrasted with a\n                corresponding epidemiological analysis. The ultimate\n                goal is to determine the inherent disadvantages of each type of implant and to discover the possible\n                risks derived from the use of PIP prostheses.</p> <p class=\"font-avenir font-semibold\">IP: Carmen Escobedo Lucea</p></div> <div class=\"w-2/6 border-l-2 border-gray-300\"><img"+(_vm._ssrAttr("src",require("assets/logos_apoti.png")))+" alt=\"Logos APOTI project\" class=\"relative top-40\"></div></div>"):"<!---->")+" "+((_vm.selectedPage === 'UPHOT')?("<div class=\"flex container mx-auto pt-20 pb-20 h-auto md:w-2/3\"><div class=\"w-4/6 flex flex-col h-auto\"><a href=\"#\" title=\"Go Back\"><svg xmlns=\"http://www.w3.org/2000/svg\" fill=\"none\" viewBox=\"0 0 24 24\" stroke-width=\"1.5\" stroke=\"currentColor\" class=\"w-10 h-10\"><path stroke-linecap=\"round\" stroke-linejoin=\"round\" d=\"M11.25 9l-3 3m0 0l3 3m-3-3h7.5M21 12a9 9 0 11-18 0 9 9 0 0118 0z\"></path></svg></a> <h1 class=\"w-full flex  text-6xl pt-16 pb-16 font-raleway\">NIM-UPHOT</h1> <p class=\"flex w-full pb-28 font-avenir pr-8 text-justify\">Infertility is a pathological condition affecting\n                one eighth of women and one tenth of men worldwide\n                and accentuates the declining European birth rates. Assisted reproductive techniques (ART) have been\n                developed to treat infertility, and in vitro\n                fecundation (IVF) has become the golden treatment with the highest pregnancy rates. According to recent\n                studies, there is a need for novel\n                techniques to reach higher efficiency and better control of the in vitro conditions.<br><br>This project\n                seeks to improve cell culture systems and\n                incorporate novel non-invasive monitoring techniques for IVF. Our proposal explores the opportunities\n                offered by ultrafast pulse laser beams to\n                provide new solutions and enhance IVF's essential operations. The two main research lines presented are\n                the following: (a) the development of new\n                &quot;lab-on-a-chip&quot; culture systems based on microfluidic devices, and (b) the adaptation of different\n                ultrafast laser characterization and detection\n                techniques to non-invasive monitoring of biological samples such as culture media or embryos. This is an\n                ambitious and applied project with the\n                capacity to generate frontier knowledge, which is supported by the experience and capabilities of a\n                multidisciplinary team that brings together\n                physicists, biologists, and engineers. Developments from the project will produce a significant impact\n                on both ultrafast lasers research by bringing\n                new systems based on the fundamental concepts presented in recent years by the research team, and on the\n                biomedical field with potential advances\n                in the functionality of the routinary experimental techniques. </p> <p class=\"font-avenir font-semibold\">IP: Carmen Escobedo Lucea</p></div> <div class=\"w-2/6 border-l-2 border-gray-300\"><img"+(_vm._ssrAttr("src",require("assets/logos_nimuphot.png")))+" alt=\"Logos NIM-UPHOT project\" class=\"relative top-40\"></div></div>"):"<!---->")+" "+((_vm.selectedPage === 'INVESTIGO')?("<div class=\"flex container mx-auto pt-20 pb-20 h-auto md:w-2/3\"><div class=\"w-4/6 flex flex-col h-auto\"><a href=\"#\" title=\"Go Back\"><svg xmlns=\"http://www.w3.org/2000/svg\" fill=\"none\" viewBox=\"0 0 24 24\" stroke-width=\"1.5\" stroke=\"currentColor\" class=\"w-10 h-10\"><path stroke-linecap=\"round\" stroke-linejoin=\"round\" d=\"M11.25 9l-3 3m0 0l3 3m-3-3h7.5M21 12a9 9 0 11-18 0 9 9 0 0118 0z\"></path></svg></a> <h1 class=\"w-full flex  text-6xl pt-16 pb-16 font-raleway\">INVESTIGO</h1> <p class=\"flex w-full pb-28 font-avenir pr-4 text-justify\">From the Department of Innovation, Universities,\n                Science and Digital Society, funding has been obtained\n                for a total of four contracts with the &quot;Programa Investigo&quot;, which aims to hire young researchers,\n                technologists and technical staff in the field\n                of R+D+i (Research, Development and Innovation). The activities to be supported are focused on\n                initiatives related to health, the ecological\n                transition and green economy, covering thematic areas such as renewable energies, energy efficiency,\n                water and waste treatment, and the agri-food\n                industry. This call seeks to strengthen the capacity for research and technological development in\n                various sectors, promoting the employability of\n                young talents in activities related to R+D+i. Through the recruitment of qualified personnel, the aim is\n                to promote the generation of knowledge,\n                innovation and technology transfer in strategic areas for scientific, technological and socioeconomic\n                progress.</p> <p class=\"font-avenir font-semibold\">IP: Carmen Escobedo Lucea</p></div> <div class=\"w-2/6 border-l-2 border-gray-300\"><img"+(_vm._ssrAttr("src",require("assets/logos_investigo.png")))+" alt=\"Logos INVESTIGO project\" class=\"relative top-40\"></div></div>"):"<!---->")+" "+((_vm.selectedPage === 'BANVIRTE')?("<div class=\"flex container mx-auto pt-20 pb-20 h-auto md:w-2/3\"><div class=\"w-4/6 flex flex-col h-auto\"><a href=\"#\" title=\"Go Back\"><svg xmlns=\"http://www.w3.org/2000/svg\" fill=\"none\" viewBox=\"0 0 24 24\" stroke-width=\"1.5\" stroke=\"currentColor\" class=\"w-10 h-10\"><path stroke-linecap=\"round\" stroke-linejoin=\"round\" d=\"M11.25 9l-3 3m0 0l3 3m-3-3h7.5M21 12a9 9 0 11-18 0 9 9 0 0118 0z\"></path></svg></a> <h1 class=\"w-full flex  text-6xl pt-16 pb-16 font-raleway\">BANVIRTE</h1> <p class=\"flex w-full pb-28 font-avenir pr-4 text-justify\">BANVIRTE will create a database through the\n                digitization of tissues stored in the Tissue and Cell Bank of the Valencian Community with the aim of\n                optimizing the process of choosing the most suitable graft prior to transplantation. The collaboration\n                between the Tissue and Cell Bank of the Valencian Community and BTELab falls within this strategic\n                framework, where the aim is to take advantage of the infrastructure available at the Fundaci√≥n del\n                Hospital General Universitario de Valencia for the management of anatomical images, in order to generate\n                a &quot;virtual database&quot;. As a result, it would be possible to add two fundamental parameters to the basic\n                concept of surgery: personalization of treatment and maximum precision.<br><br>This challenge represents\n                a novelty at international level with a notable impact in the field of tissue transplantation,\n                incorporating a pioneering environment of &quot;personalized precision surgery&quot;. Therefore, as described\n                above, this should be understood as an improvement in the quality of medical services and greater\n                satisfaction of patients and healthcare professionals.</p> <p class=\"font-avenir font-semibold\">IP: Carmen Escobedo Lucea</p></div> <div class=\"w-2/6 border-l-2 border-gray-300\"><img"+(_vm._ssrAttr("src",require("assets/Logo_Fundaci√≥n.png")))+" alt=\"Logo Fundaci√≥n Investigaci√≥n HGUV\" class=\"relative top-40 m-8\"> <img"+(_vm._ssrAttr("src",require("assets/Logo_Consorcio.png")))+" alt=\"Logo Consorcio HGUV\" class=\"relative top-48 m-8\"></div></div>"):"<!---->")+" "+((_vm.selectedPage === 'ATILA')?("<div class=\"flex container mx-auto pt-20 pb-20 h-auto md:w-2/3\"><div class=\"w-4/6 flex flex-col h-auto\"><a href=\"#\" title=\"Go Back\"><svg xmlns=\"http://www.w3.org/2000/svg\" fill=\"none\" viewBox=\"0 0 24 24\" stroke-width=\"1.5\" stroke=\"currentColor\" class=\"w-10 h-10\"><path stroke-linecap=\"round\" stroke-linejoin=\"round\" d=\"M11.25 9l-3 3m0 0l3 3m-3-3h7.5M21 12a9 9 0 11-18 0 9 9 0 0118 0z\"></path></svg></a> <h1 class=\"w-full flex  text-6xl pt-16 pb-16 font-raleway\">ATILA</h1> <p class=\"flex w-full pb-28 font-avenir pr-4 text-justify\">ATiLA: Pioneering advances in metal additive\n                manufacturing for personalized health care. The ATiLA project is presented as a model of innovation in\n                the field of metal additive manufacturing (MAM), focusing specifically on the technique of wire-based\n                laser additive manufacturing (WLAM) or wire laser metal deposition (LWMD). The goal is not only to\n                define precise process specifications, but also to optimize osseointegration and mitigate potential\n                problems, such as infections or traction mismatches.<br><br>What sets ATiLA apart is its potential to\n                revolutionize MAM technologies, as it offers significant cost and energy savings compared to existing\n                methodologies. Aligning with &quot;Industry 4.0&quot; principles, the project advocates hybridization of MAMs,\n                improving flexibility and reducing material waste in traditional manufacturing processes. The technology\n                and prototype development follows a digital twin (DT) approach, incorporating various modeling\n                methodologies, sensing and probabilistic methods.<br><br>The success of the ATiLA project could\n                significantly benefit the healthcare sector by reducing costs and increasing the specificity of\n                implants. The prospect of personalized medicine becomes more tangible, addressing a critical challenge\n                in healthcare, where budget constraints often limit access to personalized stents. Its development\n                depends on a collaborative alliance with key partners. AIDIMME's expertise in titanium materials science\n                is instrumental in mastering the complexities of the Ti64-ELI alloy. MELTIO's manufacturing process\n                know-how is the main contribution to prototype development. FIHGUV, in addition to its expertise in\n                biocompatibility, plays a dual role, as it not only ensures compliance with health standards, but is\n                also responsible for the design of models for subsequent manufacturing, which encompasses the field of\n                industrial design.</p> <p class=\"font-avenir font-semibold\">IP: Carmen Escobedo Lucea</p></div> <div class=\"w-2/6 border-l-2 border-gray-300\"><img"+(_vm._ssrAttr("src",require("assets/logo-plan-de-recuperacion.png")))+" alt=\"Logo Plan Recuperaci√≥n\" class=\"relative top-40 m-8\"> <img"+(_vm._ssrAttr("src",require("assets/Logo_Ministeriociencia.png")))+" alt=\"Logo Ministerio Ciencia\" class=\"relative top-48 m-8\"> <img"+(_vm._ssrAttr("src",require("assets/Logo_NextGeneration.png")))+" alt=\"Logo Next Generation\" class=\"relative top-56 m-8\"> <img"+(_vm._ssrAttr("src",require("assets/Logo_AEI.png")))+" alt=\"Logo AEI\" class=\"relative h-40 top-64 left-24 m-8\"></div></div>"):"<!---->")+" "+((_vm.selectedPage === 'SAIBSAL')?("<div class=\"flex container mx-auto pt-20 pb-20 h-auto md:w-2/3\"><div class=\"w-4/6 flex flex-col h-auto\"><a href=\"#\" title=\"Go Back\"><svg xmlns=\"http://www.w3.org/2000/svg\" fill=\"none\" viewBox=\"0 0 24 24\" stroke-width=\"1.5\" stroke=\"currentColor\" class=\"w-10 h-10\"><path stroke-linecap=\"round\" stroke-linejoin=\"round\" d=\"M11.25 9l-3 3m0 0l3 3m-3-3h7.5M21 12a9 9 0 11-18 0 9 9 0 0118 0z\"></path></svg></a> <h1 class=\"w-full flex  text-6xl pt-16 pb-16 font-raleway\">SAIBSAL</h1> <p class=\"flex w-full pb-28 font-avenir pr-4 text-justify\">SAIBSAL, managed by FIHGUV and integrated into\n                the ISCIII platform, is a benchmark of excellence in biomedical research. The main mission is to promote\n                scientific, health and biomedical research through the provision of cutting-edge services.<br><br>Within\n                the scope of the services offered by SAIBSAL, the fields of 3D printing and bioprinting stand out,\n                offering unique solutions. In addition, our expertise extends to the competent management of cell lines\n                and the biofabrication of preclinical models, contributing significantly to the advancement of therapies\n                and the deepening of our understanding of human pathologies.<br><br>SAIBSAL is committed to raising the\n                level of biobanking services, adhering to rigorous biological and histological standards in accordance\n                with ISO 20387. Strategically, SAIBSAL seeks to streamline access to biobanking services. Visibility and\n                collaboration are paramount. SAIBSAL actively participates in collaborative efforts through social\n                networks and patient associations. Sharing innovations and results collectively contributes to shaping\n                the research landscape. In the area of financial sustainability, SAIBSAL works hard to secure funding to\n                ensure the continuity of our activities and resources.</p> <p class=\"font-avenir font-semibold\">IP: Carmen Escobedo Lucea</p></div> <div class=\"w-2/6 border-l-2 border-gray-300\"><img"+(_vm._ssrAttr("src",require("assets/logo-plan-de-recuperacion.png")))+" alt=\"Logo Plan Recuperaci√≥n\" class=\"relative top-40 m-8\"> <img"+(_vm._ssrAttr("src",require("assets/Logo_ISCIII.png")))+" alt=\"Logo ISCIII\" class=\"relative top-48 m-8\"> <img"+(_vm._ssrAttr("src",require("assets/Logo_Ministeriociencia.png")))+" alt=\"Logo Ministerio Ciencia\" class=\"relative top-56 m-8\"> <img"+(_vm._ssrAttr("src",require("assets/Logo_cofinanciadoporUE.png")))+" alt=\"Logo AEI\" class=\"relative top-64 m-8\"></div></div>"):"<!---->"))])}
var staticRenderFns = []

export { render, staticRenderFns }